/* Add this to your existing CSS */

.icon-menu-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    padding: 20px 0;
  }
  
  .icon-menu-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
  }
  
  /* Update the styling for the icons */
  .icon-menu-item img,
  .icon-menu-item svg {
    border-radius: 50%;
    background-color: #3498db; /* You can change the background color */
    padding: 15px; /* Adjust the padding as needed */
    width: 50px; /* Example size, adjust as needed */
    height: auto;
    margin-bottom: 8px; /* Space between the icon and text */
    transition: background-color 0.3s ease-in-out;
  }
  
  .icon-menu-item p {
    font-size: 16px;
    text-align: center;
  }
  
  /* Add hover effect */
  .icon-menu-item:hover img,
  .icon-menu-item:hover svg {
    background-color: #2c3e50; /* Change the background color on hover */
    transform: scale(1.1); /* Scales the icon up to 110% on hover */
    color: white; /* Change the color on hover */
  }
  
  @media (max-width: 768px) {
    .icon-menu-container {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
      gap: 10px;
      padding: 10px;
    }
  
    .icon-menu-item {
      background: #fff;
      border: 1px solid #eaeaea;
      padding: 15px;
      box-shadow: 0 4px 8px rgba(0,0,0,0.1);
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
      height: 150px;
    }
  
    .icon-menu-item p {
      margin-top: 8px;
    }
  
    /* Ensure that slick slider does not apply its own styles on top of your grid */
    .mobile-carousel .slick-slide > div {
      margin: 0;
      padding: 0;
    }
  }
  
  